

 @import"../../global.scss";

.topbar{

  overflow: hidden;
  width: 100%;
  height:70px;
  background-color: white;
   position: fixed;
   color:$primaryColor;
  top:0;
  z-index: 3;
  transition: all 1s ease;

  .wrapper{
    padding:10px 8px ;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .left{
       display: flex;
       align-items: center;  
    

      .itemContainer{
        display: flex;
        
        
      



        .homeContainer{
          a{
          display: flex;
          align-items: center;
          justify-content: center;
          margin-right: 35px;
          margin-left: 5px;
          position: relative;
         
          height: 50px;
          
          width: 50px;
          
         border-radius: 3px;
          overflow: hidden;
          margin-top: 0px;
          margin-bottom: 20px;
          cursor: pointer;
           left: 0;
          &:hover{
            box-shadow: 1px 1px 0px 1px;
          }
        }

        }
           .linkedinHolder{
            display: flex;
            align-items: center;
            justify-content: center;
            margin-right: 5px;
            margin-left: 5px;
           
            height: 50px;
            width: 50px;
            
           border-radius: 3px;
            overflow: hidden;
            margin-top: 0px;
            margin-bottom: 13px;
            cursor: pointer;
            &:hover{
              box-shadow: 1px 1px 0px 1px;
            }
           }

           .instaHolder{
            display: flex;
            align-items: center;
            justify-content: center;
            margin-right: 5px;
            margin-left: 4px;
           
            height: 51px;
            width: 51px;
            
           border-radius: 14px;
            overflow: hidden;
            font-weight: 0px;
            
            
            cursor: pointer;
            &:hover{
              box-shadow: 1px 1px 1px 1px;

            }

            


           }
 
           .gitHolder{

            display: flex;
            align-items: center;
            justify-content: center;
            margin-right: 5px;
           
            height: 52px;
            width: 53px;
            
           border-radius: 25px;
            overflow: hidden;
            
            cursor: pointer;
            &:hover{
              box-shadow: 1px 1px 1px 1px;
            }
            
           }

         

           span{
             font-size: 15px;
             font-weight: 700;

           }
      }
    }

    .right{

      .hamburger {
          
        width: 35px;
        height:28px;
        display: flex;
         flex-direction: column;
         justify-content: space-between;
         cursor: pointer;
         margin-bottom: 20px;
         margin-right: 5px;

         span {
           
          width: 100%;
          height: 3px;
          background-color:$mainColor;
          transform-origin: left;
          transition: all 2s ease;
        }
      }

     
    }
  }



  &.active{

  //the &. mean is there is a active

  background-color: $mainColor;
     color: $secondaryColor; 
     overflow: hidden;

     .hamburger {


       span{
         &:first-child{
           background-color: red;
           transform: rotate(45deg);
         }
         &:last-child{
           background-color: red;
           transform: rotate(-45deg);
         }
         &:nth-child(2){
           opacity: 0;
         }
       }
     }

     .wrapper .left .logo{
       color: $secondaryColor;
     }

     
  }
}


